import React from 'react';
import kh_project from '../images/kh_project_img.jpg';
import git_img from '../images/github.png';
import styled from 'styled-components';
import { generateMedia } from 'styled-media-query';

// 반응형 웹
const customMedia = generateMedia({
    maxmobile: '575px',
    minmobile: '352px',
});

const Projectdisplay = styled.div`
    position: relative;
    display: flex;
    flex-wrap: wrap;
    background-color: #FBFBEF;
    height: auto;
`
const Index = styled.div`
    float: left;
    height: fit-content;
    white-space: pre-line;
    margin-top: 1.5vw;
    margin-left: 1.5vw;
    font-family: JejuGothic, NanumGothic;
    font-size: 1.2vw;
    font-weight: 900;
`
const Project_h1 = styled.h1`
    margin: 0 auto;
`
const Project_title = styled.div`
    position: absolute;
    margin-top: 3vw;
    width: fit-content;
    height: fit-content;
    left: 50%;
    transform:translateX(-50%);
    text-decoration: none;
    border-bottom: 0.15vw solid #FACC2E;
    padding-bottom:0.5vw;
    font-family: JejuGothic, NanumGothic;
    font-size: 1.5vw;

    ${customMedia.lessThan('maxmobile')`
        font-size: 3vw;
    `}
`
const Scroll_body = styled.div`
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    right: 0;
    overflow-x: scroll;
    // ${customMedia.lessThan('maxmobile')`
    //     width: 97%;
    // `}
    // ${customMedia.lessThan('minmobile')`
    //     width: 94%;
    // `}
`
const Project_body = styled.div`
    display: flex;
    flex-direction: row;
    margin-top: 3vw;
    width: 100%;
    height: auto;
    right: 0;
    flex-shrink: 0;

    ${customMedia.lessThan('maxmobile')`
        display: flex;
        flex-direction: column;
        margin-top: 5vw;
    `}
`
const Project_detail_title = styled.h1`
    margin-left: 2vw;
    margin-bottom: 1vw;
    width: 85%;
    font-family: JejuGothic, NanumGothic;
    font-size: 2vw;

    ${customMedia.lessThan('maxmobile')`
        margin-bottom: 3vw;
        text-align: center;
        width: 100%;
        font-size: 3vw;
    `}
`
const Kh_project = styled.div`
    border-radius: 0.5vw;
    box-shadow: 0 10px 15px -3px rgba(0,0,0,.1), 
                0 4px 6px -2px rgba(0,0,0,.05);
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    margin-left: 8vw;
    margin-right: 8vw;
    margin-bottom: 2vw;
    width: 90%;

    ${customMedia.lessThan('maxmobile')`
        display: flex;
        flex-direction: column;
        width: 85%;
        margin-left: 7.5vw;
    `}
`
const Kh_project_img = styled.div`
    display: flex;
    margin-left: 2vw;
    align-items: center;
    width: 35%;
    height: auto;
    margin-right: 1vw;

    ${customMedia.lessThan('maxmobile')`
        margin: 0 auto;
        margin-bottom: 2vw;
        width: 80%;
        height: auto;
    `}
`
const Kh_img = styled.img`
    width: 100%;
    height: auto;
`
const Project_content_title = styled.h1`
    margin-bottom: 1vw;
    width: 85%;
    font-family: JejuGothic, NanumGothic;
    font-size: 2vw;

    ${customMedia.lessThan('maxmobile')`
        margin-bottom: 1vw;
        width: 100%;
        font-family: JejuGothic, NanumGothic;
        font-size: 4vw;
    `}
`
const Kh_project_content = styled.div`
    margin-left: 1vw;
    display: flex;
    flex-direction: column;
    text-align: justify;
    width: 60%;
    height: auto;

    ${customMedia.lessThan('maxmobile')`
        margin: 0 auto;
        width: 80%;
    `}
`
const Project_introduction = styled.div`
    margin-bottom: 4vw;
    white-space: pre-line;
    font-family: JejuGothic, NanumGothic;
    font-size: 1vw;
    line-height: 2vw;

    ${customMedia.lessThan('maxmobile')`
        margin-bottom: 3vw;
        font-size: 3vw;
        line-height: 4vw;
    `}
`
const Project_myrule = styled.div`
    white-space: pre-line;
    font-family: JejuGothic, NanumGothic;
    font-size: 1vw;
    line-height: 2vw;

    ${customMedia.lessThan('maxmobile')`
        font-size: 3vw;
        line-height: 4vw;
    `}
`
const Skill = styled.div`
    display: flex;
    flex-wrap: wrap;
    margin: 0 auto;
    margin-top: 2vw;
    margin-left: 2vw;
    margin-bottom: 1vw;
    width: 85%;
    height: fit-content;

    ${customMedia.lessThan('maxmobile')`
        margin: 0 auto;
        margin-top: 2vw;
        width: 80%;
    `}
`
const Skills = styled.div`
    cursor: pointer;
    color: rgb(31, 100, 165);
    margin-right: 2vw;
    width: fit-content;
    height: fit-content;
    font-family: JejuGothic, NanumGothic;
    font-size: 1.5vw;

    ${customMedia.lessThan('maxmobile')`
        font-size: 2.5vw;
        line-height: 3vw;
    `}
`
const Git = styled.div`
    width: fit-content;
    height: auto;
    margin: 2vw;
        
    ${customMedia.lessThan('maxmobile')`
        width: fit-content;
        height: auto;
        margin: 2vw;
        margin-left: 90%;
    `}
`
const Github = styled.img`
    width: 4vw;
    height: auto;
            
    ${customMedia.lessThan('maxmobile')`
        width: 6vw;
        height: auto;
    `}
`
const Left = styled.div`
    cursor: pointer;
    content: '';
    opacity: 0.7;
    &:before {
        content: '';
        position: absolute;
        top: 48%;
        left: 3vw;
        height: 3vw;
        border: solid black;
        border-radius: 5px;
        border-width: 0 0.7vw 0.7vw 0;
        transform: rotate(45deg);
    }
    &:after {
        content: '';
        position: absolute;
        top: 52%;
        left: 3vw;
        height: 3vw;
        border: solid black;
        border-radius: 5px;
        border-width: 0 0.7vw 0.7vw 0;
        transform: rotate(-45deg);
    }

    ${customMedia.lessThan('maxmobile')`
        &:before {
            top: 49%;
            border-width: 1.6vw 1vw 1vw 0;
        }
        &:after {
            top: 51%;
            border-width: 1.6vw 1vw 1vw 0;
        }
    `}
`
const Right = styled.div`
    cursor: pointer;
    content: '';
    opacity: 0.7;
    &:before {
        content: '';
        position: absolute;
        top: 48%;
        right: 3vw;
        height: 3vw;
        border: solid black;
        border-radius: 5px;
        border-width: 0 0.7vw 0.7vw 0;
        transform: rotate(-45deg);
    }
    &:after {
        content: '';
        position: absolute;
        top: 52%;
        right: 3vw;
        height: 3vw;
        border: solid black;
        border-radius: 5px;
        border-width: 0 0.7vw 0.7vw 0;
        transform: rotate(45deg);
    }

    ${customMedia.lessThan('maxmobile')`
        &:before {
            top: 49%;
            border-width: 1.6vw 1vw 1vw 0;
        }
        &:after {
            top: 51%;
            border-width: 1.6vw 1vw 1vw 0;
        }
    `}
`

class Project extends React.Component {

    scrollToLeft () {
        this.scrollLeft(document.getElementById('scroll'), -(document.getElementById('scroll').offsetWidth), 10);   
    }

    scrollToRight () {
        this.scrollLeft(document.getElementById('scroll'), document.getElementById('scroll').offsetWidth, 10);   
    }

    scrollLeft = (element, change, duration) => {
        var start = element.scrollLeft,
            currentTime = 0,
            increment = 0.5;
            
            console.log(start)
            
        var animateScroll = () => {        
            currentTime += increment;
            var val = this.easeInOutQuad(currentTime, start, change, duration);
            element.scrollLeft = val;
            if(currentTime < duration) {
                setTimeout(animateScroll, 0);
            }
        };
        animateScroll();
    }

    //t = current time
    //b = start value
    //c = change in value
    //d = duration
    easeInOutQuad = function (t, b, c, d) {
        t /= d/2;
            if (t < 1) return c/2*t*t + b;
            t--;
            return -c/2 * (t*(t-2) - 1) + b;
    };

    render() {
        return (
            <Projectdisplay id = "Projectdisplay">
                <Index>SHIN <br></br> ; <br></br> PORT <br></br> FOLIO</Index>
                <Project_title><Project_h1>Project</Project_h1></Project_title>
                <Scroll_body id = "scroll">
                    <Project_body  id = "Kh_project">
                        <Kh_project>
                            <Project_detail_title>KHOB(취업정보 사이트)</Project_detail_title>
                            <Kh_project_img>
                                <Kh_img src = { kh_project } alt = "kh_img"/>
                            </Kh_project_img>

                            <Kh_project_content>
                                <Project_content_title>Introduction</Project_content_title>
                                <Project_introduction>
                                    주제 : 취업 정보 통합 포털사이트<br></br>
                                    인원 : 5명(팀장 포함)<br></br>
                                    배경 : 기업에 관한 정보와 취업 정보를 무료로 조회하는 사이트가 존재하지 않음 → "기업조회 + 정보 제공하는 웹사이트"<br></br>
                                    참고 모델 사이트 : 크레딧잡 + 잡코리아 + 자소설 닷컴<br></br>
                                    진행기간 : 2019.06.19 ~ 2019.08.12<br></br>
                                </Project_introduction>
                                <Project_content_title>MyRule</Project_content_title>
                                <Project_myrule>
                                    메인 홈페이지 기업검색 기능<br></br>
                                    검색한 기업의 간략한 정보 게시판<br></br>
                                    기업리뷰게시판 (게시글 CRUD, 게시글 추천, 댓글 작성 및 추천)<br></br>
                                </Project_myrule>
                            </Kh_project_content>

                            <Skill>
                                <Skills>#Java</Skills>
                                <Skills>#SpringFramework(MVC)</Skills>
                                <Skills>#JSP</Skills>
                                <Skills>#CSS</Skills>
                                <Skills>#JavaScript</Skills>
                                <Skills>#Ajax</Skills>
                                <Skills>#Oracle</Skills>
                            </Skill>

                            <a href = "https://github.com/JoonHyun94/Final">
                                <Git>
                                    <Github src = { git_img } alt = "github"/>
                                </Git>
                            </a>
                        </Kh_project>
                    </Project_body>

                    <Project_body id = "Pf_project">
                        <Kh_project>
                            <Project_detail_title>KHOB(취업정보 사이트)</Project_detail_title>
                            <Kh_project_img>
                                <Kh_img src = { kh_project } alt = "kh_img"/>
                            </Kh_project_img>

                            <Kh_project_content>
                                <Project_content_title>Introduction</Project_content_title>
                                <Project_introduction>
                                    주제 : 취업 정보 통합 포털사이트<br></br>
                                    인원 : 5명(팀장 포함)<br></br>
                                    배경 : 기업에 관한 정보와 취업 정보를 무료로 조회하는 사이트가 존재하지 않음 → "기업조회 + 정보 제공하는 웹사이트"<br></br>
                                    참고 모델 사이트 : 크레딧잡 + 잡코리아 + 자소설 닷컴<br></br>
                                    진행기간 : 2019.06.19 ~ 2019.08.12<br></br>
                                </Project_introduction>
                                <Project_content_title>MyRule</Project_content_title>
                                <Project_myrule>
                                    메인 홈페이지 기업검색 기능<br></br>
                                    검색한 기업의 간략한 정보 게시판<br></br>
                                    기업리뷰게시판 (게시글 CRUD, 게시글 추천, 댓글 작성 및 추천)<br></br>
                                </Project_myrule>
                            </Kh_project_content>

                            <Skill>
                                <Skills>#Java</Skills>
                                <Skills>#SpringFramework(MVC)</Skills>
                                <Skills>#JSP</Skills>
                                <Skills>#CSS</Skills>
                                <Skills>#JavaScript</Skills>
                                <Skills>#Ajax</Skills>
                                <Skills>#Oracle</Skills>
                            </Skill>
                            
                            <a href = "https://github.com/JoonHyun94/Final">
                                <Git>
                                    <Github src = { git_img } alt = "github"/>
                                </Git>
                            </a>
                        </Kh_project>
                    </Project_body>
                </Scroll_body>
                <Left onClick = { () => this.scrollToLeft() }></Left>
                <Right onClick = { () => this.scrollToRight() }></Right>
            </Projectdisplay>
        )
    }
}

export default Project;